text.substitution = (sub, player, mode) => {
    const originalMode = text.mode;
    mode = mode || text.mode;
    const order = {
        0 : text(' 1st', mode),
        1 : text(' 2nd', mode),
        2 : text(' 3rd', mode),
        3 : text(' 4th', mode),
        4 : text(' 5th', mode),
        5 : text(' 6th', mode),
        6 : text(' 7th', mode),
        7 : text(' 8th', mode),
        8 : text(' 9th', mode)
    }[player.order];
    const position = text.fielderShortName(player.position, mode);

    if (mode === 'n') {
        text.mode = 'n';
        var output = `${sub.getName() + text.comma() + player.getName()}${text.comma()}${order}(${position})`;
    } else {
        text.mode = 'e';
        output = `${sub.getName()} replaces ${player.getName()} at ${position}, batting${order}`;
    }
    text.mode = originalMode;
    return output;
};

text.getBattersEye = game => {
    const eye = {}, breaking = Math.abs(game.pitchInFlight.breakDirection[0]) + Math.abs(game.pitchInFlight.breakDirection[1]) > 40;
    eye.e =
        text('looks like: ', 'e')+
        breaking ? text('breaking ball', 'e') : text('fastball', 'e');
    eye.n =
        text('looks like: ', 'n')+
        breaking ? text('breaking ball', 'n') : text('fastball', 'n');
    return eye;
};

text.baseShortName = base => {
    if (text.mode == 'n') {
        return {
            '1st': '',
            '2nd': '',
            '3rd': '',
            'home' : '',
            'Home' : '',

            'left': '',
            'center': '',
            'right': ''
        }[base];
    }
    return base;
};

text.fielderShortName = (fielder, override) => {
    const mode = override || text.mode;
    if (mode === 'n') {
        return {
            'first': '',
            'second': '',
            'third': '',
            'short': '',
            'pitcher': '',
            'catcher': '',
            'left': '',
            'center': '',
            'right': ''
        }[fielder];
    }
    return fielder;
};

text.slash = () => {
    if (text.mode == 'n') {
        return '';
    }
    return '/';
};

text.fielderLongName = fielder => {
    if (text.mode == 'n') {
        return {
            'first': '',
            'second': '',
            'third': '',
            'short': '',
            'pitcher': '',
            'catcher': '',
            'left': '',
            'center': '',
            'right': ''
        }[fielder]
    }
    return {
        first : text('first baseman'),
        second : text('second baseman'),
        third : text('third baseman'),
        short : text('shortstop'),
        pitcher : text('pitcher'),
        catcher : text('catcher'),
        left : text('left fielder'),
        center : text('center fielder'),
        right : text('right fielder')
    }[fielder];
};

text.comma = () => ({n: '', e: ', '})[text.mode];
text.space = () => ({n: '', e: ' '})[text.mode];
text.stop = () => ({n: '', e: '. '})[text.mode];

text.namePitch = pitch => {
    if (text.mode == 'e') {
        return pitch.name.charAt(0).toUpperCase() + pitch.name.slice(1)
    }
    if (text.mode == 'n') {
        return text(pitch.name)
    }
};

text.contactResult = (batter, fielder, bases, outBy, sacrificeAdvances, out) => {
    let statement = '';
    const infield = ['left', 'center', 'right'].indexOf(fielder) < 0;
    const doublePlay = out.doublePlay;
    if (text.mode == 'e') {
        statement += batter;
        if (outBy) {
            switch (outBy) {
                case 'fieldersChoice':
                    play = out.length === 2 ? 'double play ' : '';
                    statement += ` reached on a fielder's choice ${play}by ${text.fielderShortName(fielder)}`;
                    break;
                case 'line':
                    statement += ` lined out to ${text.fielderShortName(fielder)}`;
                    break;
                case 'fly':
                    statement += ` flew out to ${text.fielderShortName(fielder)}`;
                    break;
                case 'error':
                    statement += ` reached on error by ${text.fielderShortName(fielder)}`;
                    break;
                case 'pop':
                    statement += ` popped out to ${text.fielderShortName(fielder)}`;
                    break;
                case 'ground':
                    var play = doublePlay ? 'into a double play by' : 'out to';
                    statement += ` grounded ${play} ${text.fielderShortName(fielder)}`;
                    break;
                case 'thrown':
                    play = doublePlay ? ' on a double play' : '';
                    statement += ` was thrown out by ${text.fielderShortName(fielder)}${play}`;
                    break;
            }
            if (out.length) {
                const plural = out.length > 1;
                const runner = plural ? 'Runners' : 'Runner';
                const is = plural ? 'are' : 'is';
                statement += `. ${runner} from ${text(out.join(text.comma()))} ${is} out`;
            }
        } else {
            switch (bases) {
                case 1:
                    if (infield) {
                        statement += ` reached on an infield hit to ${text.fielderShortName(fielder)}`;
                    } else {
                        statement += ` reached on a single to ${text.fielderShortName(fielder)}`;
                    }
                    break;
                case 2:
                    statement += ` doubled past ${text.fielderShortName(fielder)}`;
                    break;
                case 3:
                    statement += ` tripled past ${text.fielderShortName(fielder)}`;
                    break;
                case 4:
                    statement += ` homered to ${text.fielderShortName(fielder)}`;
                    break;
            }
        }
        if (sacrificeAdvances) {
            sacrificeAdvances.map(base => {
                if (base == 'third') {
                    statement += `${text.stop()}Runner on third scores`;
                } else {
                    statement += `${text.stop()}Runner on ${base} advances`;
                }
            });
        }
        statement += text.stop();
    }
    if (text.mode == 'n') {
        const stop = text.stop();
        statement += `${batter}`;
        if (outBy) {
            const fielderLong = text.fielderLongName(fielder);
            fielder = text.fielderShortName(fielder);
            switch (outBy) {
                case 'fieldersChoice':
                    statement += `(${fielder})`;
                    break;
                case 'line':
                    statement += `${fielder}`;
                    break;
                case 'fly':
                    statement += `${fielder}`;
                    break;
                case 'error':
                    statement += `(${fielder})`;
                    break;
                case 'pop':
                    statement += `${fielder}`;
                    break;
                case 'ground':
                    statement += `${fielderLong}`;
                    break;
                case 'thrown':
                    statement += `${fielder}`;
                    break;
            }
            if (out.length) {
                statement += `${out.map(runner => text(runner)).join(text.comma())}`;
            }
            if (doublePlay) {
                statement += '';
            }
        } else {
            fielder = text.fielderShortName(fielder);
            switch (bases) {
                case 1:
                    if (infield) {
                        statement += `(${fielder})`;
                    } else {
                        statement += `(${fielder})`;
                    }
                    break;
                case 2:
                    statement += `${fielder}`;
                    break;
                case 3:
                    statement += `${fielder}`;
                    break;
                case 4:
                    statement += `${fielder}`;
                    break;
            }
        }
        if (sacrificeAdvances) {
            sacrificeAdvances.map(base => {
                if (base === 'third') {
                    statement += `${stop}`;
                } else {
                    statement += `${stop + text(base)}`;
                }
            });
        }
        statement += stop;
    }
    return statement;
};

export { text }

export const abbreviatePosition = function (position) {
    if (text.mode === 'e') {
        return {
            pitcher : 'P',
            catcher : 'C',
            first : '1B',
            second : '2B',
            short : 'SS',
            third : '3B',
            left : 'LF',
            center : 'CF',
            right : 'RF'
        }[position];
    }
    return text.fielderShortName(position);
};



